@media screen and (max-width:767px) {
    .footer .footer__top
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80%;
        min-height: auto;
        
    }
    .footer__logo 
    {
        margin-bottom: 2rem;
        text-align: center;
    }
    .footer__logo p
    {
        font-size: 1.2rem;
        color:white;
        text-align: center;
    }
    .footer__logo .footer__logo-name
    {
        font-size: 2.5rem;
        color:white;
        text-transform: uppercase;
    }
    .footer__menu
    {
        width: 80%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .footer__menu Link , a 
    {
        font-size: 1.3rem;
        margin-right: 0rem;
    }
    .footer__social
    { 
        margin-top: 2rem;  
        width: 30%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .footer__social a
    {
        margin-right: 0rem;    
    }

}