@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --backgroundcolor:rgb(25,22,39);
    --emailTitle:rgb(255,255,255);
    --normalWord:rgb(188,186,196);
    --hover : #ff501a;
    --border:rgb(82, 82, 82);
    --socialColor:rgb(110,87,224);
    --socialHover:rgba(110,87,224,0.8);
}

body 
{
    font-family: 'Roboto', sans-serif;
}

html 
{
    font-size: 62.5%;
}

.Contact 
{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 70%;
    margin: 10rem auto 0;
    
}

.Contact__title
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 4rem;
    
}

.Contact__title h1 
{
 color:var(--nameTitle);
 font-size: 3.6rem;
}
.Contact__title p
{
 color:var(--normalWord);
 font-size: 1.4rem;
}

.Contact__main 
{
    display: flex;
    flex-direction: row;
    width:100%;
    justify-content: space-around;
    align-items: center;
    min-height: 90%;
}

.Contact__main-personal 
{
    display: flex;
    flex-direction: column;
    transform: translateY(-53%);
    margin-right: 5rem;
    min-width: 30%;
    --TitleSize: 1.7rem;
    --Iconsize: 1.8rem;
    --wordNormal: 1.4rem;
}

/* call */
.personal__call
{
    display: flex;
    flex-direction: row;
    align-items: center; 
    min-width: fit-content;
    margin-bottom: 2rem;
}

.personal__call i
{
    padding:1rem;
    font-size: var(--Iconsize);
    color: var(--socialColor);
}

.personal__call-info
{
    display: flex;
    flex-direction: column;
    line-height: 1.5; 
}

.personal__call-info h3
{
   font-size: var(--TitleSize);
   color:var(--nameTitle);
}
.personal__call-info span
{
   font-size: var(--wordNormal);
   color:var(--normalWord);
}

/* email */
.personal__email
{
    display: flex;
    flex-direction: row;
    align-items: center; 
    margin-bottom: 2rem;
}

.personal__email i
{
    padding:1rem;
    font-size: var(--Iconsize);
    color: var(--socialColor);
}

.personal__email-info
{
    display: flex;
    flex-direction: column;
    line-height: 1.5; 
}

.personal__email-info h3
{
   font-size: var(--TitleSize);
   color:var(--nameTitle);
}
.personal__email-info span
{
   font-size: var(--wordNormal);
   color:var(--normalWord);
}

/* address */
.personal__address
{
    display: flex;
    flex-direction: row;
    align-items: center; 
}

.personal__address i
{
    padding:1rem;
    font-size: 2.26rem;
    color: var(--socialColor);
    
}

.personal__address-info
{
    display: flex;
    flex-direction: column;
    line-height: 1.5; 
}

.personal__address-info h3
{
   font-size: var(--TitleSize);
   color:var(--nameTitle);
}
.personal__address-info span
{
   font-size: var(--wordNormal);
   color:var(--normalWord);
}


/* form */
.Contact__main-form
{
    display: flex;
    flex-direction: column;
    min-width: 60%;
    height: 100%;
}

.Contact__main-info 
{
    display: flex;
    flex-direction: row;
}
/* main_name */
.Contact__main-name
{
    display: flex;
    flex-direction: column;
    width:50%;
    height: 80%;
    background-color: rgb(33,29,53);
    color:var(--normalWord);
    border-radius: 0.7rem;
    margin-right: 1rem;
    
}

.Contact__main-name input 
{
    outline: none;
    border:none;
    border-bottom: 1px solid rgb(185, 185, 185);
    background-color: transparent;
    margin:0rem 1rem 1rem;
    color: var(--wordNormal);
}

.Contact__main-name label 
{
    padding: 1rem 1rem;
    font-size: 1.6rem;
}

/* main_email */
.Contact__main-email
{
    display: flex;
    flex-direction: column;
    width:50%;
    height: 80%;
    background-color: rgb(33,29,53);
    color:var(--normalWord);
    border-radius: 0.7rem;
    
}

.Contact__main-email input 
{
    outline: none;
    border:none;
    border-bottom: 1px solid rgb(185, 185, 185);
    background-color: transparent;
    margin:0rem 1rem 1rem;
    color: var(--wordNormal);
}

.Contact__main-email label 
{
    padding: 1rem 1rem;
    font-size: 1.6rem;
}

/* project */
.Contact__main-project
{
    display: flex;
    flex-direction: column;
    width:100%;
    min-height: 80%;
    background-color: rgb(33,29,53);
    color:var(--normalWord);
    border-radius: 0.7rem;
    margin-top: 2rem;
    
}

.Contact__main-project input 
{
    outline: none;
    border:none;
    border-bottom: 1px solid rgb(185, 185, 185);
    background-color: transparent;
    margin:0rem 1rem 1rem;
    color: var(--wordNormal);
}

.Contact__main-project label 
{
    padding: 1rem 1rem;
    font-size: 1.6rem;
}

/* message */
.Contact__main-message
{
    display: flex;
    flex-direction: column;
    width:100%;
    min-height: 80%;
    background-color: rgb(33,29,53);
    color:var(--normalWord);
    border-radius: 0.7rem;
    margin-top: 2rem;
    
}

.Contact__main-message textarea
{
    outline: none;
    border:none;
    border-bottom: 1px solid rgb(185, 185, 185);
    background-color: transparent;
    margin:0rem 1rem 1rem;
    color: var(--wordNormal);
    height: 10rem;
}

.Contact__main-message label 
{
    padding: 1rem 1rem;
    font-size: 1.6rem;
}

.Contact__main-form button 
{
    margin-top: 1rem;
    width: 17rem;
    height: 6rem;
    background-color: var(--socialColor);
    color: var(--emailTitle);
    outline: none;
    border:none;
    border-radius: 0.7rem;
    cursor: pointer;
    font-size: 1.6rem;
    
}

.Contact__main-form button i
{
    margin-left: 0.6rem;
}

