@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --backgroundcolor:rgb(25,22,39);
    --nameTitle:rgb(255,255,255);
    --normalWord:rgb(188,186,196);
    --hover : #ff501a;
    --border:rgb(82, 82, 82);
}

body 
{
    font-family: 'Roboto', sans-serif;
    height: 100%;
    margin: 0;
}

html 
{
    
    font-size: 62.5%;
}

.notFound
{
    width: 70%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin:1rem auto;
    /* background-color: rebeccapurple; */
}

.notFound .a
{
    width: 100%;
    height: 60rem;
    background-image: url("https://image.freepik.com/free-vector/no-data-concept-illustration_114360-626.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}

.notFound h3
{
   font-size: 3rem;
   font-weight: 500;
   color: var(--nameTitle);
    text-align: center;
    padding:1rem 0 5rem;
}