@media screen and (max-width:767px) {
    
    .Contact__title
    {
        margin-bottom: 15rem;
        
    }
    .Contact__main-personal 
    {
        display: flex;
        flex-direction: column;
        transform: translateY(-53%);
        margin-right: 0rem;
        min-width: 30%;
        --TitleSize: 1.7rem;
        --Iconsize: 1.8rem;
        --wordNormal: 1.4rem;
    }
    .Contact__main 
    {
        display: flex;
        flex-direction: column;
        
    }
    .Contact__main-info 
    {
        display: flex;
        flex-direction: column;
    }
    .Contact__main-form
    {
        min-width: 100%;
        height: 100%;
    }
    .Contact__main-name
    {
        width:100%;
        height: 80%;
        margin-right: 0rem;
        margin-bottom: 2rem;
        
    }
    .Contact__main-email
    {
        width:100%;
        height: 80%;
        margin-right: 0rem;
        
    }
    .Contact__main-form button 
    {
        margin-top: 1rem;
        width: 13rem;
        height: 5rem;

        font-size: 1.3rem;
        margin: 1rem auto;
    }
}