  --background : rgb(25,22,39);
  position: relative;
  min-height: 100vh;
  background-color: var(--background);
}

.App .Header__browser-modal
{
  display: flex;
  flex-direction: column;
  width:30%;
  height: 100vh;
  position: fixed;
  top:0;
  left:-100%;
  background-color: white;
  z-index: 10;
  
}

.App .Header__browser-modal .Header__search-mobile
{
    display: flex;
    flex-direction: row;
    width: 80%;
    height: 8%;
    position: relative;
    color: black;
    background-color: transparent;
    margin:3rem auto;
    display: none;
}

.App .Header__browser-modal .Header__search-mobile i
{
  position: absolute;
  z-index: 10;
  top:50%;
  left: 5%;
  -webkit-transform: translate(-20%,-50%);
          transform: translate(-20%,-50%);
  font-size: 2rem;
}

.App .Header__browser-modal .Header__search-mobile input
{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  z-index: 0;
  padding: 1rem 6rem;
  background-color: white;
  color: black;
  border: 1px solid black;
  outline: none;
  border-radius: 2rem;
  font-size: 1rem;
}

.App .Header__browser-modal .Header__info
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin:10rem 3.7rem;
  line-height: 2;
  width: 80%;
  border-top: 1px solid rgb(163, 163, 163);
  border-bottom: 1px solid rgb(163, 163, 163);
  padding:1rem;
}

.App .Header__browser-modal .Header__info p
{
  font-weight: 600;
  color: black;
  font-size: 2.0rem;
}

.App .Header__browser-modal .Header__info .phimbo , .phimle ,.phimhoathinh, .phimchieurap
{
  text-decoration: none;
  color: var(--normalWord);
  font-size: 1.4rem;
  transition: all 0.2s linear;
  
}

.App .Header__browser-modal .Header__info .phimbo:hover , .phimle:hover ,.phimhoathinh:hover, .phimchieurap:hover
{
  color: var(--socialColor);

  
}

.Header__browser-modal .closeModal
{
  position: absolute;
  top:0;
  right: 0;
  color:black;
  font-size: 3rem;
  padding: 1rem 1rem 3rem 1rem;
  cursor: pointer;
}

.Header__browser-modal .closeModal:hover
{
  color:var(--socialColor);
}


.Header__info a 
{
  text-decoration: none;
  color: var(--normalWord);
  font-size: 1.4rem;
  transition: all 0.2s linear;
}

.Header__info a:hover
{
  color: var(--socialColor);
}



@-webkit-keyframes fadein{
  0% {
    opacity: 0;
  }
  30% 
  {
    opacity: 0.3;
  }
  70% 
  {
    opacity: 0.7;
  }
  100% 
  {
    opacity: 1;
  }
}



@keyframes fadein{
  0% {
    opacity: 0;
  }
  30% 
  {
    opacity: 0.3;
  }
  70% 
  {
    opacity: 0.7;
  }
  100% 
  {
    opacity: 1;
  }
}

