{
    #Header__navbar
    {
        display: block;
        color:whitesmoke;
        font-size: 2rem;
        cursor: pointer;
    }
    .Header__search
    {
        display: none;
    }
    .Header__browse
    {

        display: none;
    }
    .Header__logo
    {
        margin-left: -15%;
