*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --backgroundcolor:rgb(25,22,39);
    --nameTitle:rgb(255,255,255);
    --normalWord:rgb(188,186,196);
    --hover : #ff501a;
    --border:rgb(82, 82, 82);
}

body 
{
    font-family: 'Roboto', sans-serif;
}

html 
{
    font-size: 62.5%;
}

.Phimbo 
{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    margin-top: calc(1rem);
    
    background-color: transparent;
    position: relative;
   
}

.Phimbo .fa-angle-right
{
    position: absolute;
    top:50%;
    right:5%;
    background-color: var(--nameTitle);
    font-size: 2rem;
    padding:1rem 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.15s ease-in-out;
}

.Phimbo .fa-angle-right:hover
{
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.Phimbo .fa-angle-left
{
    position: absolute;
    top:50%;
    left:5%;
    background-color: var(--nameTitle);
    font-size: 2rem;
    padding:1rem 1.5rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.15s ease-in-out;
}

.Phimbo .fa-angle-left:hover
{
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.Phimbo__title
{
    display: flex;
    width: 80%;
    flex-direction: row;
    justify-content: space-between;  
    font-size: 2rem;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s linear;
    color: white;
    margin:-2rem auto 4rem;
    
}

.Phimbo__title h2:hover
{
   color:var(--hover);
}

.Phimbo__title i:hover
{
    color:var(--hover);
}


.Phimbo__product 
{
    display: flex;
    
    width: 80%;
    overflow-x: scroll;
    white-space: nowrap;
    flex-direction: row;
    /* flex-wrap: wrap; */
    margin: 0 auto;
    /* justify-content: space-around; */
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    -ms-scroll-snap-points-x: repeat(300px);
    scroll-snap-points-x: repeat(300px);
    -ms-scroll-snap-type: mandatory;
    scroll-snap-type: mandatory;
     /* border-bottom: 1px solid var(--border); */
}

.Phimbo__product::-webkit-scrollbar {
    display: none;
  }



/* sản phẩm phim bộ */
.Phimbo__product-cart
{
    width: calc(100% / 4 - 1rem);
    height: 42rem;
    /* border : 1px solid blue; */
    position: relative;
    color:var(--nameTitle);
    margin-right: 1rem;
    flex: 0 1 auto;
    
}

.Phimbo__product-cart:first-child
{
 
    margin-left: auto;
}


.Phimbo__product-cart img 
{
    position: relative;
    top:0;
    left: 0;
    width: 100%;
    height: 70%;
    object-fit: cover;
    cursor: pointer;
    border-radius: 0.8rem;
}

.Phimbo__product-cart img:hover
{
    -webkit-filter: grayscale(70%);
            filter: grayscale(70%);

}

.Phimbo__product-cart i 
{
    position: absolute;
    top:35%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border:2px solid white;
    padding:2rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 2rem;
}
.Phimbo__product-cart i:hover
{
    color: rgb(223, 108, 0);
    border:2px solid rgb(223, 108, 0);
}
.Phimbo__product-cart .add 
{
    position: absolute;
    top:65%;
    left: 50%;
    -webkit-transform: translate(-50%,-60%);
            transform: translate(-50%,-60%);
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
    color: white;
    width: 95%;
    padding: 1.5rem 2rem;
    font-size: 1.4rem;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s linear;
    display: none;
    /* animation: bottom_top 1s linear 1; */
}

.Phimbo__product-cart .add:hover 
{ 
    color: #ff501a;
}

/* sự kiện cho add list */

@-webkit-keyframes bottom_top
{
    0%
    {
        opacity: 0;
        top:85%;
    }
    50%
    {
        opacity: 0;
        top:75%;
    }
    75%
    {
        opacity: 0.1;
        top:70%;
    }
    100%
    {
        opacity: 0.4;
        top:65%;
    }
}

@keyframes bottom_top
{
    0%
    {
        opacity: 0;
        top:85%;
    }
    50%
    {
        opacity: 0;
        top:75%;
    }
    75%
    {
        opacity: 0.1;
        top:70%;
    }
    100%
    {
        opacity: 0.4;
        top:65%;
    }
}

/* sự kiện mouse out */
@-webkit-keyframes top_bottom
{
    0%
    {
        opacity: 1;
        top:65%;
        
    }
    50%
    {
        opacity: 0.5;
        top:70%;
    }
    75%
    {
        opacity: 0;
        top:75%;
    }
    100%
    {
        opacity: 0;
        top:85%;
        display: none;
    }
}
@keyframes top_bottom
{
    0%
    {
        opacity: 1;
        top:65%;
        
    }
    50%
    {
        opacity: 0.5;
        top:70%;
    }
    75%
    {
        opacity: 0;
        top:75%;
    }
    100%
    {
        opacity: 0;
        top:85%;
        display: none;
    }
}


.Phimbo__product-cart .title
{ 
    margin-top: 1rem;
    font-size: 1.7rem;
    font-weight: 600;
    color:white;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; 
}

.Phimbo__product-cart .btn
{ 
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin:2rem 0 1rem;
    color:var(--normalWord);
    font-size: 1.2rem;
}

.Phimbo__product-cart .btn div
{ 
    background-color:rgb(81,81,87) ;
    padding:0.2rem 1.3rem; 
    color:white;
}


.Phimbo__product-cart .category
{ 
    font-size: 1.2rem;
    color:var(--normalWord);
}






@media screen and (max-width:767px)
{
    .Phimbo__product
    {
        --cot:2;
        padding-bottom: 4rem;
    }
    .Phimbo__product-cart .btn 
    {
        display: none;
    }
    .Phimbo__product-cart .category
    {
        display: none;
    }
    .Phimbo__product-cart .add
    {
        display: none;
    }
    .Phimbo__product-cart
    {
        width: calc(100% / var(--cot) - 1rem);
        height: 30rem;
        /* border : 1px solid blue; */
        position: relative;
        color:var(--nameTitle);
    }
    .Phimbo .fa-angle-right
    {
        top:70%;
        right:30%;
    }


    .Phimbo .fa-angle-left
    {

        top:70%;
        left:30%;
    }
}

@media screen and (min-width:600px) and (max-width:1079px)
{
    .Phimbo__product
    {
        --cot: 3;
        padding-bottom: 4rem;
    }
    .Phimbo__product-cart .btn 
    {
        display: none;
    }
    .Phimbo__product-cart .category
    {
        display: none;
    }
    .Phimbo__product-cart .add
    {
        display: none;
    }
    .Phimbo__product-cart
    {
        width: calc(100% / var(--cot) - 1rem);
        height: 30rem;
        /* border : 1px solid blue; */
        position: relative;
        color:var(--nameTitle);
    }
    .Phimbo .fa-angle-right
    {
        top:70%;
        right:30%;
    }


    .Phimbo .fa-angle-left
    {

        top:70%;
        left:30%;
    }
