    width: 80%;
    overflow-x: scroll;
    flex-direction: row;
    /* flex-wrap: wrap; */
    margin: 0 auto;
    /* justify-content: space-around; */
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    -ms-scroll-snap-points-x: repeat(300px);
    scroll-snap-points-x: repeat(300px);
    -ms-scroll-snap-type: mandatory;
    scroll-snap-type: mandatory;
     overflow-y: hidden;
    
}

.Phimle__product::-webkit-scrollbar {
    display: none;
  }



/* sản phẩm phim bộ */
.Phimle__product-cart
{
    width: calc(100% / 4 - 0.6rem);
    height: 42rem;
    /* border : 1px solid blue; */
    position: relative;
    color:var(--nameTitle);
    margin-right: 1rem;
    flex-grow: 1;
   
}

.Phimle__product-cart:first-child
{
 
    margin-left: auto;
}


.Phimle__product-cart img 
{
    position: relative;
    top:0;
    left: 0;
    width: 100%;
    height: 70%;
    object-fit: cover;
    cursor: pointer;

}

.Phimle__product-cart img:hover
{
    -webkit-filter: grayscale(70%);
            filter: grayscale(70%);

}

.Phimle__product-cart i 
{
    position: absolute;
    top:35%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border:2px solid white;
    padding:2rem;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 2rem;
}
.Phimle__product-cart i:hover
{
    color: rgb(223, 108, 0);
    border:2px solid rgb(223, 108, 0);
}
.Phimle__product-cart .add 
{
    position: absolute;
    top:65%;
    left: 50%;
    -webkit-transform: translate(-50%,-60%);
            transform: translate(-50%,-60%);
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
    color: white;
    width: 95%;
    padding: 1.5rem 2rem;
    font-size: 1.4rem;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s linear;
    display: none;
    /* animation: bottom_top 1s linear 1; */
}

.Phimle__product-cart .add:hover 
{ 
    color: #ff501a;
}

/* sự kiện cho add list */

@-webkit-keyframes bottom_top
{
    0%
    {
        opacity: 0;
        top:85%;
    }
    50%
    {
        opacity: 0;
        top:75%;
    }
    75%
    {
        opacity: 0.1;
        top:70%;
    }
    100%
    {
        opacity: 0.4;
        top:65%;
    }
}

@keyframes bottom_top
{
    0%
    {
        opacity: 0;
        top:85%;
    }
    50%
    {
        opacity: 0;
        top:75%;
    }
    75%
    {
        opacity: 0.1;
        top:70%;
    }
    100%
    {
        opacity: 0.4;
        top:65%;
    }
}

/* sự kiện mouse out */
@-webkit-keyframes top_bottom
{
    0%
    {
        opacity: 1;
        top:65%;
        
    }
    50%
    {
        opacity: 0.5;
        top:70%;
    }
    75%
    {
        opacity: 0;
        top:75%;
    }
    100%
    {
        opacity: 0;
        top:85%;
        display: none;
    }
}
@keyframes top_bottom
{
    0%
    {
        opacity: 1;
        top:65%;
        
    }
    50%
    {
        opacity: 0.5;
        top:70%;
    }
    75%
    {
        opacity: 0;
        top:75%;
    }
    100%
    {
        opacity: 0;
        top:85%;
        display: none;
    }
}


.Phimle__product-cart .title
{ 
    margin-top: 1rem;
    font-size: 1.7rem;
    font-weight: 600;
    color:white;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; 
}

.Phimle__product-cart .btn
{ 
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin:2rem 0 1rem;
    color:var(--normalWord);
    font-size: 1.2rem;
}

.Phimle__product-cart .btn div
{ 
    background-color:rgb(81,81,87) ;
    padding:0.2rem 1.3rem; 
    color:white;
}


.Phimle__product-cart .category
{ 
    font-size: 1.2rem;
    color:var(--normalWord);
}






@media screen and (max-width:767px)
{
    .Phimle__product
    {
        padding-bottom: 4rem;
    }
