    font-size: 62.5%;
}


.Banner
{
    width:100%;
    height: 35rem;
    /* background-color: red; */
    margin:5rem auto;
    display: flex;
    flex-direction: row;
}

.Banner__social 
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    list-style: none;
    line-height: 1.5;
    margin:10rem 0 0 20rem;
    transition: all 0.2s ease-out;
}

.Banner__social li a 
{
    cursor: pointer;
    color:var(--socialColor);
    font-size : 2rem;
}

.Banner__social li a:hover
{
    color: var(--socialHover);

    /* opacity:0.9; */
}

.Banner__info 
{
    display: flex;
    flex-direction: column;
    margin:3rem 10rem 4rem;
    line-height: 1.5;
}

.Banner__info h1 
{
    color:white;
    font-size: 4.8rem;
    text-transform: uppercase;
}

.Banner__info h5
{
    color:var(--normalWord);
    font-size: 2rem;
    font-weight: 500;
}

.Banner__info p
{
    color:var(--normalWord);
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 2rem;
}

#btn_watch 
{
    width:10rem;
    height: 4.5rem;
    color:white;
    background-color: var(--socialColor);
    border:none;
    outline: none;
    border-radius: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.2s linear;
    margin-bottom: 4rem;
    transition: all 0.2s ease;
}

#btn_watch:hover
{
    background-color: var(--socialHover);
}

.Banner__info .Banner-scrollDown
{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    
    align-items: center;
    font-size: 1.6rem;
    width:15rem;
    padding:1rem 1rem;
    cursor: pointer;
    
}

.Banner__info .Banner-scrollDown:hover
{
    -webkit-animation: moveScroll 1s linear 1;
            animation: moveScroll 1s linear 1;
}

.Banner__info .Banner-scrollDown i
{
    color:var(--socialColor);
    font-size: 1.8rem;
    margin-right: 0.5rem;
}
.Banner__info .Banner-scrollDown span
{
    color:white;
    margin-right: 0.5rem;
}


@-webkit-keyframes moveScroll 
{
 0% 
 {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
 }
 25%
 {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
 }
 50%
 {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
 }
 75%
 {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
 }
 100%
 {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
 }
}


@keyframes moveScroll 
{
 0% 
 {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
 }
 25%
 {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
 }
 50%
 {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
 }
 75%
 {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
 }
 100%
 {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
 }
}
@media screen and (max-width:767px)
{
    .Banner__social 
    {
        margin:8rem 0 0 4rem;
    }
    .Banner__info 
    {

        margin:3rem 5rem 0;
