    height: auto;
    margin:4rem auto 2rem;
    /* align-items: center; */
}

.footer__logo p
{
    font-size: 1.5rem;
    color:white;
}
.footer__logo .footer__logo-name
{
    font-size: 3rem;
    color:white;
    text-transform: uppercase;
}

.footer__menu Link , a 
{
    text-decoration: none;
    color:white;
    transition:all 0.2s ease-in-out;
    font-size: 1.8rem;
    margin-right: 2rem;
}

.footer__menu Link:hover , a:hover 
{
   color:var(--socialColor);
    cursor: pointer;
    
}


.footer__social a
{
    margin-right: 2rem;    
}

.footer .copyright
{
    color: var(--nameTitle);
    font-size: 1.5rem;
    margin: 4rem auto;
}
@media screen and (max-width:767px) {
    .footer .footer__top
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80%;
        min-height: auto;
        
    }
    .footer__logo 
    {
        margin-bottom: 2rem;
        text-align: center;
    }
    .footer__logo p
    {
        font-size: 1.2rem;
        color:white;
        text-align: center;
    }
    .footer__logo .footer__logo-name
    {
        font-size: 2.5rem;
        color:white;
        text-transform: uppercase;
    }
    .footer__menu
    {
        width: 80%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .footer__menu Link , a 
    {
        font-size: 1.3rem;
        margin-right: 0rem;
    }
    .footer__social
    { 
        margin-top: 2rem;  
        width: 30%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .footer__social a
    {
        margin-right: 0rem;    
    }

}
